#storie-button {
    height: 15vw;
    width: 15vw;
    max-width: 50px;
    max-height: 50px;
    border-radius: 100%;
    border: solid 2px  var(--color-3);
    overflow: hidden;
}

.storie-photo {
    width: 100%;
    height: 100%;
    object-fit: cover;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    user-select: none;
}
